import { Card } from "@/components/ui/card"
import { Progress } from "@/components/ui/progress"
import { AnalysisResult as AnalysisResultType } from "@/types"

interface AnalysisResultProps {
  result: AnalysisResultType
}

export function AnalysisResult({ result }: AnalysisResultProps) {
  return (
    <Card className="p-6 bg-black/50 backdrop-blur-sm border-gray-800">
      <div className="space-y-6">
        <div className="text-center">
          <h3 className="text-2xl font-bold text-white mb-2">
            {result.isFake ? "⚠️ Likely Fake" : "✅ Likely Authentic"}
          </h3>
          <p className="text-gray-400">
            Analysis completed at {new Date(result.analysisTime).toLocaleString()}
          </p>
        </div>

        <div className="space-y-4">
          <div>
            <div className="flex justify-between mb-2">
              <span className="text-white">Confidence Score</span>
              <span className="text-white">{result.confidence.toFixed(1)}%</span>
            </div>
            <Progress value={result.confidence} className="h-2" />
          </div>

          <div>
            <div className="flex justify-between mb-2">
              <span className="text-white">Manipulation Detection</span>
              <span className="text-white">{result.details.manipulationScore.toFixed(1)}%</span>
            </div>
            <Progress value={result.details.manipulationScore} className="h-2" />
          </div>

          <div>
            <div className="flex justify-between mb-2">
              <span className="text-white">Authenticity Score</span>
              <span className="text-white">{result.details.authenticity.toFixed(1)}%</span>
            </div>
            <Progress value={result.details.authenticity} className="h-2" />
          </div>
        </div>

        {result.details.regions.length > 0 && (
          <div>
            <h4 className="text-lg font-semibold text-white mb-3">Detected Manipulated Regions</h4>
            <div className="space-y-2">
              {result.details.regions.map((region, index) => (
                <div key={index} className="text-sm text-gray-400">
                  Region {index + 1}: {region.confidence.toFixed(1)}% confidence
                </div>
              ))}
            </div>
          </div>
        )}
      </div>
    </Card>
  )
}
