"use client"

import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { useState } from "react"
import Image from "next/image"
import { AnalysisResult } from "@/components/AnalysisResult"
import { LoadingAnalysis } from "@/components/LoadingAnalysis"
import { toast } from "sonner"
import { useAuth } from "@/context/auth-context"

export default function Home() {
  const { user } = useAuth()
  const [selectedImage, setSelectedImage] = useState<File | null>(null)
  const [isAnalyzing, setIsAnalyzing] = useState(false)
  const [analysisResult, setAnalysisResult] = useState<any>(null)
  const [previewUrl, setPreviewUrl] = useState<string | null>(null)

  const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0]
      setSelectedImage(file)
      setPreviewUrl(URL.createObjectURL(file))
      setAnalysisResult(null)
    }
  }

  const handleAnalyze = async () => {
    if (!selectedImage) return

    if (!user) {
      toast.error('Please sign in to analyze images')
      return
    }

    setIsAnalyzing(true)
    try {
      const formData = new FormData()
      formData.append('image', selectedImage)

      const response = await fetch('/api/analyze', {
        method: 'POST',
        body: formData,
      })

      if (!response.ok) {
        throw new Error('Analysis failed')
      }

      const result = await response.json()
      setAnalysisResult(result)
      toast.success('Analysis completed successfully')
    } catch (error) {
      console.error('Error analyzing image:', error)
      toast.error('Failed to analyze image. Please try again.')
    } finally {
      setIsAnalyzing(false)
    }
  }

  return (
    <main className="flex min-h-screen flex-col">
      {/* Hero Section */}
      <section className="relative h-screen">
        <div className="absolute inset-0 bg-gradient-to-br from-blue-900 to-black">
          <div className="absolute inset-0 opacity-30 mix-blend-overlay bg-[url('/network-pattern.png')]" />
        </div>
        
        <div className="relative container mx-auto px-4 pt-32">
          <div className="max-w-3xl">
            <h1 className="text-5xl font-bold text-white mb-6">
              Deep Fake Detection
            </h1>
            <p className="text-xl text-gray-200 mb-8">
              Detect manipulated images with our advanced AI technology. Upload your image and get instant analysis.
            </p>

            <div className="space-y-6">
              <Card className="p-6 bg-black/50 backdrop-blur-sm border-0">
                <div className="space-y-4">
                  <Input
                    type="file"
                    accept="image/*"
                    onChange={handleImageUpload}
                    className="text-white"
                  />
                  {previewUrl && (
                    <div className="relative w-full h-48 rounded-lg overflow-hidden">
                      <Image
                        src={previewUrl}
                        alt="Preview"
                        fill
                        className="object-cover"
                      />
                    </div>
                  )}
                  <Button 
                    onClick={handleAnalyze}
                    disabled={!selectedImage || isAnalyzing}
                    className="w-full bg-blue-500 hover:bg-blue-600"
                  >
                    {isAnalyzing ? "Analyzing..." : "Analyze Image"}
                  </Button>
                </div>
              </Card>

              {isAnalyzing ? (
                <LoadingAnalysis />
              ) : (
                analysisResult && <AnalysisResult result={analysisResult} />
              )}
            </div>
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section className="py-20 bg-black">
        <div className="container mx-auto px-4">
          <h2 className="text-3xl font-bold text-white mb-12 text-center">How It Works</h2>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            <Card className="p-6 bg-black/50 backdrop-blur-sm border-gray-800">
              <h3 className="text-xl font-bold text-white mb-4">Upload Image</h3>
              <p className="text-gray-400">Simply upload any image you want to analyze for potential manipulation.</p>
            </Card>

            <Card className="p-6 bg-black/50 backdrop-blur-sm border-gray-800">
              <h3 className="text-xl font-bold text-white mb-4">AI Analysis</h3>
              <p className="text-gray-400">Our advanced AI model processes the image to detect signs of manipulation.</p>
            </Card>

            <Card className="p-6 bg-black/50 backdrop-blur-sm border-gray-800">
              <h3 className="text-xl font-bold text-white mb-4">Get Results</h3>
              <p className="text-gray-400">Receive detailed analysis results showing if and how the image was manipulated.</p>
            </Card>
          </div>
        </div>
      </section>
    </main>
  )
}
